.foot {
    font-family: 'sometype', Times, serif;
    font-size: 12px;
    width: 100%;
}

.footlist {
    list-style-type: none;
    margin: 0;
    bottom: 0;
    width: 100%;
    overflow: hidden;
    position: fixed;
}

.foottext {
    float: left;
    display: block;
    color: #131313;
    text-decoration: none;
    margin-bottom: 30px;
    margin-left: 20px;
    margin-right: 30px;
    display: inline;
    position: relative;
    font-weight: bold;
}

.footlink {
    color: #131313;
    text-decoration: none;
    font-weight: normal;
}

.footlink::before {
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    height: 1px;
    bottom: 0;
    left: 0;
    background-color: #131313;
    transform: scaleX(0);
    transform-origin: bottom right;
    transition: transform 0.3s ease-out;
}

.footlink:hover::before {
    transform: scaleX(1);
    transform-origin: bottom left;
}